@use "global" as *;

.info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: remf(14);
  line-height: calc(26 / 14);
}

.info__category {
  background-color: #ffea2e;
  color: $black;
  padding: 0 remf(5);
  letter-spacing: calc(1 / 14) * 1em;
}
